
.container{
  margin: -100px auto 30px auto;
  text-align: center;
}
.innercontiner{
  max-width: 1000px;
  padding-left: 0;
  padding-right: 0;
}
.nav{
  color: white;
  display: inline-block;
  margin: 0 auto;
  min-width: 727px;
}
.nav-link,.nav-link:hover,.nav-link:focus{
  background-color: #000000;
  color: white !important;
}
.nav-link.active
{
  background-color: orange;
  color: white !important;
}
.nav-tabs .nav-link:focus, .nav-tabs .nav-link:hover{
  background-color: #000000;
}
.container-text{
  min-height: 300px;
  margin-top: 5px;
}
.tab-item{
  padding: 0 1px;
}
.tab-item a{
  border-radius: 2px;
}
.font-carousel{
  padding: 10px;
  background-color: white;
}
.font-app-list-container{
}
.font-app-list{

  padding: 10px;
  clear: both;
  list-style: none;
}
.font-app-list li{
  float: left;
  width: 130px;
  height: 130px;
  background-color: #eeeeee;
  margin: 5px;
  line-height: 130px;
  vertical-align: middle;
}
.container-text-white{
  background-color: white;
}
.font-app-list a,.font-app-list a:link,.font-app-list a:visited{
  color: #666666 ;
}
.font-app-list-mask{
  line-height: 30px;
  display: inline-block;
  margin-top: 27px;
}
.font-app-list-mask img{
  width: 30px;
  height: 30px;
}
.font-app-list-mask p{
  margin-top: 5px;
}



